import "./betPanel.css";
import chip from "../../../../../assets/funCard/chips/chip_5.png";
import React from "react";

export default class BetPanel extends React.Component {
    constructor(props) {
        super(props);
        this.previous = undefined;
    }
    handleClick(e) {
        // e.preventDefault();
        if (this.previous) {
            console.log("there is previous button");
            document.getElementById(this.previous).innerHTML = `<span/>`;
        }
        // else{
        // console.log("there is no previous button");
        this.previous = e.target.id;
        // }
        console.log(e);
        this.props.action(e.target.dataset.value);

        // e.target.classList.add("betChip");
        e.target.innerHTML = `<img src=${chip} />`;
    }

    render() {
        return (
            <div className="betPanel">
                <ul className="scroll-items" onClick={this.handleClick.bind(this)}>
                    <li data-value="A" id="Ace">
                        {/* <img src={chip} /> */}
                    </li>
                    <li data-value="2" id="Two">
                        <span></span>
                    </li>
                    <li data-value="3" id="Three">
                        <span></span>
                    </li>
                    <li data-value="4" id="Four">
                        <span></span>
                    </li>
                    <li data-value="5" id="Five">
                        <span></span>
                    </li>
                    <li data-value="6" id="Six">
                        <span></span>
                    </li>
                    <li data-value="7" id="Seven">
                        <span></span>
                    </li>
                    <li data-value="8" id="Eight">
                        <span></span>
                    </li>
                    <li data-value="9" id="Nine">
                        <span></span>
                    </li>
                    <li data-value="T" id="Ten">
                        <span></span>
                    </li>
                    <li data-value="J" id="Jack">
                        <span></span>
                    </li>
                    <li data-value="Q" id="Queen">
                        <span></span>
                    </li>
                    <li data-value="K" id="King">
                        <span></span>
                    </li>
                </ul>
            </div>
        );
    }
}
