import "../../css/projects.css";
import { useState } from "react";
import { HiMenu } from "react-icons/hi";
import { Route, Switch } from "react-router";
import { useHistory, useRouteMatch } from "react-router";
import Poker from "../projects/poker";
import FunCard from "../projects/funCard/index";
import Ecom from "../projects/ecom";
import Roulette from "../projects/roulette/roulette";
import Plinko from "../projects/plinko";

const NavPanel = (props) => {
    return (
        <div>
            <div className={props.show ? "NavPanel showNavPanel" : "NavPanel"}>
                <div
                    className="content"
                    onClick={(e) => {
                        e.preventDefault();
                        props.toggleContent(true, e.target.id);
                    }}
                >
                    <div id="poker">Project 1</div>
                    <div id="roulette">Project 2</div>
                    <div id="funCard">Fun Card</div>
                    <div id="ecom">Project 4</div>
                    {/* <div id="plinko">Plinko</div> */}
                </div>
            </div>
        </div>
    );
};



const Projects = (props) => {
    const [showNav, setShowNav] = useState(false);
    const history = useHistory();
    const { path, url } = useRouteMatch();
    
    const toggleContent = (isContent, project) => {
        console.log(project);
        console.log(`${url}/${project}`);
        switch (project) {
            case "poker":
                history.push(`${url}/poker`);
                break;
            case "funCard":
                history.push(`${url}/funCard`);
                break;
            case "ecom":
                history.push(`${url}/ecommerce`);
                break;
            case "roulette":
                history.push(`${url}/roulette`);
                break;
            case "plinko":
                history.push(`${url}/plinko`);
                break;
            default:
                break;
        }
        setShowNav(!showNav);

    };

    return (
        <div className="Projects">
            <div
                className="menu"
                onClick={(e) => {
                    e.preventDefault();
                    setShowNav(!showNav);
                }}
            >
                <HiMenu size={32} />
            </div>
            <NavPanel toggleContent={toggleContent} show={showNav}></NavPanel>

            <div className="projectDescription">
                <Switch>
                    <Route path={`${path}/poker`}>
                        <Poker></Poker>
                    </Route>
                    <Route path={`${path}/ecommerce`}>
                        <Ecom></Ecom>
                    </Route>
                    <Route path={`${path}/roulette`}>
                        <Roulette></Roulette>
                    </Route>
                    <Route path={`${path}/funCard`}>
                        <FunCard></FunCard>
                    </Route>
                    <Route path={`${path}/plinko`}>
                        <Plinko></Plinko>
                    </Route>
                </Switch>
            </div>
        </div>
    );
};

export default Projects;
