import "../../css/contact.css";
import { FcCellPhone } from "react-icons/fc";
import { AiOutlineLinkedin } from "react-icons/ai";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPhone, faEnvelope } from "@fortawesome/free-solid-svg-icons";

const Footer = () => {
    return (
        <div className="Footer">
            <div className="title">Contact Info</div>

            <div className="info">
                <div>
                    <FontAwesomeIcon icon={faPhone} />
                    <span>+447879957397</span>
                </div>
                <div>
                    <AiOutlineLinkedin />
                    <span>linkedIn</span>
                </div>
                <div>
                    <FontAwesomeIcon icon={faEnvelope} />
                    <span>kunreddy.naveen@gmail.com</span>
                </div>
            </div>
        </div>
    );
};

const Contact = (props) => {
    return (
        <div className="Contact">
            <div className="Content">
                <div className="contentBox">Write some content here</div>
                <div className="form">

                <form>
                  <input className="formElements" type="text" placeholder="Name"></input>
                  <input className="formElements" type="number" placeholder="Contact Number"></input>
                  <input className="formElements" type="email" placeholder="E mail ID"></input> 
                  <textarea className="formElements formText" defaultValue="Message"></textarea> 
                  <input className="formSubmit" type="submit" onClick={(e)=>{e.preventDefault()}}></input> 
                </form>
                </div>
            </div>
            <Footer></Footer>
        </div>
    );
};

export default Contact;
