import { Stage, Layer, Rect, Text, Group } from "react-konva";
import React from "react";
// import Card from './card';
import Cards from "./cards";
import cardSound from "../../../../../assets/funCard/Card.wav";
import winSound from "../../../../../assets/funCard/win.mp3";
// import BetPanel from "./betPanel";
import BetPanel from "./betPanel";
import Img from "./image";
import "./index.css";
import "./betButton.css";
import dealerImg from "../../../../../assets/funCard/dealer.png";
import userImg from "../../../../../assets/funCard/user_avtar.png";
import cardDisp from "../../../../../assets/funCard/cardDisp.png";
import tableBg from "../../../../../assets/funCard/table.png";
// import dealer3D from "../assets/3dimg.png";

export default class Game extends React.Component {
    constructor(props) {
        super(props);

        if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
            console.log("yes its mobile");
            this.isMobile = true;
            this.width = window.screen.height;
            this.height = window.screen.width;
        } else {
            console.log("not a mobile");
            this.isMobile = false;
            this.width = props.width;
            this.height = props.height;
        }

        this.state = {
            systemCards: [],
            playerCards: [],
            winner: "The Winner is...",
            cdWidth: 100,
            cpWidth: 100,
        };

        this.mapCards = {
            Ac: [0, 0, 53, 73],
            "2c": [53, 0, 53, 73],
            "3c": [106, 0, 53, 73],
            "4c": [159, 0, 53, 73],
            "5c": [212, 0, 53, 73],
            "6c": [265, 0, 53, 73],
            "7c": [318, 0, 53, 73],
            "8c": [371, 0, 53, 73],
            "9c": [424, 0, 53, 73],
            Tc: [477, 0, 53, 73],
            Jc: [530, 0, 53, 73],
            Qc: [583, 0, 53, 73],
            Kc: [636, 0, 53, 73],

            Ad: [0, 73, 53, 73],
            "2d": [53, 73, 53, 73],
            "3d": [106, 73, 53, 73],
            "4d": [159, 73, 53, 73],
            "5d": [212, 73, 53, 73],
            "6d": [265, 73, 53, 73],
            "7d": [318, 73, 53, 73],
            "8d": [371, 73, 53, 73],
            "9d": [424, 73, 53, 73],
            Td: [477, 73, 53, 73],
            Jd: [530, 73, 53, 73],
            Qd: [583, 73, 53, 73],
            Kd: [636, 73, 53, 73],

            Ah: [0, 146, 53, 73],
            "2h": [53, 146, 53, 73],
            "3h": [106, 146, 53, 73],
            "4h": [159, 146, 53, 73],
            "5h": [212, 146, 53, 73],
            "6h": [265, 146, 53, 73],
            "7h": [318, 146, 53, 73],
            "8h": [371, 146, 53, 73],
            "9h": [424, 146, 53, 73],
            Th: [477, 146, 53, 73],
            Jh: [530, 146, 53, 73],
            Qh: [583, 146, 53, 73],
            Kh: [636, 146, 53, 73],

            As: [0, 219, 53, 73],
            "2s": [53, 219, 53, 73],
            "3s": [106, 219, 53, 73],
            "4s": [159, 219, 53, 73],
            "5s": [212, 219, 53, 73],
            "6s": [265, 219, 53, 73],
            "7s": [318, 219, 53, 73],
            "8s": [371, 219, 53, 73],
            "9s": [424, 219, 53, 73],
            Ts: [477, 219, 53, 73],
            Js: [530, 219, 53, 73],
            Qs: [583, 219, 53, 73],
            Ks: [636, 219, 53, 73],
            xx: [689, 0, 53, 73],
            xx1: [689, 73, 53, 73],
            xx2: [742, 0, 53, 73],
            xx3: [689, 146, 53, 73],
            xx4: [689, 219, 53, 73],
        };
        this.cardsArray = ["Ac", "2c", "3c", "4c", "5c", "6c", "7c", "8c", "9c", "Tc", "Jc", "Qc", "Kc", "Ad", "2d", "3d", "4d", "5d", "6d", "7d", "8d", "9d", "Td", "Jd", "Qd", "Kd", "Ah", "2h", "3h", "4h", "5h", "6h", "7h", "8h", "9h", "Th", "Jh", "Qh", "Kh", "As", "2s", "3s", "4s", "5s", "6s", "7s", "8s", "9s", "Ts", "Js", "Qs", "Ks"];
        this.cardsArrayBuf = ["A", "2", "3", "4", "5", "6", "7", "8", "9", "T", "J", "Q", "K", "A", "2", "3", "4", "5", "6", "7", "8", "9", "T", "J", "Q", "K", "A", "2", "3", "4", "5", "6", "7", "8", "9", "T", "J", "Q", "K", "A", "2", "3", "4", "5", "6", "7", "8", "9", "T", "J", "Q", "K"];
        this.sound = new Audio(cardSound);
        this.winnerSound = new Audio(winSound);

        this.cardOne = React.createRef();

        this.cards_system = [];
        this.cards_player = [];
        this.used = [];

        this.isPlayers = false;
        this.betCard = 0;
        this.avtar_p_ref = React.createRef();
        this.avtar_d_ref = React.createRef();
    }

    componentDidMount() {}
    dealHandler(e) {
        console.log("bet card is: " + this.betCard);
        if (this.betCard !== 0 && this.betCard !== undefined) {
            this.startDealing(this.betCard);
        } else {
            console.log("chose you bet card");
        }
    }
    startDealing(bet) {
        console.log("dealing called--bet card is: " + bet);
        this.int3 = setInterval(() => {
            this.wD = this.state.cdWidth;
            this.wP = this.state.cpWidth;
            function randomCard() {
                return Math.floor(Math.random() * 52);
            }
            // this.used.push(n);
            // this.used.pop();
            let n = randomCard();
            if (this.used.includes(n)) {
                // console.log("card already used");
                return;
            } else {
                this.used.push(n);

                if (this.isPlayers) {
                    this.cards_player.push(this.cardsArray[n]);
                    this.setState({ playerCards: this.cards_player, cpWidth: this.wP + 35 });
                    this.isPlayers = !this.isPlayers;
                    this.sound.play();
                } else {
                    this.cards_system.push(this.cardsArray[n]);
                    this.setState({ systemCards: this.cards_system, cdWidth: this.wD + 35 });
                    this.isPlayers = !this.isPlayers;
                    this.sound.play();
                }
                if (this.cardsArrayBuf[n] === bet) {
                    clearInterval(this.int3);
                    console.log(this.used);
                    // console.log("is Player????" + this.isPlayers);
                    this.winner(this.isPlayers);
                    return;
                }
                console.log("player cards: " + this.cards_player);
                console.log("player cards: " + this.cards_system);
            }
        }, 500);
    }
    winner(isDealer) {
        this.winnerSound.play();
        if (isDealer) {
            console.log("Dealer won the game");
            this.setState({ winner: "Dealer won the game" });
            this.avtar_d_ref.current.animate();
        } else {
            console.log("you won the game");
            this.avtar_p_ref.current.animate();
            this.setState({ winner: "You won the game" });
        }
        setTimeout(() => {
            this.clearGame();
        }, 2000);

        // this.cards_player = [];
        // this.cards_system = [];
        this.used = [];
        this.isPlayers = false;
        this.betCard = 0;
        // setTimeout(()=>{
        //     // this.setState({playerCards:[],systemCards:[],winner:"The Winner is...",cdWidth:100,cpWidth:100});
        //     // this.setState({winner:"The Winner is..."});
        // },5000)
    }
    clearGame() {
        this.clearInt = setInterval(() => {
            this.wDc = this.state.cdWidth;
            this.wPc = this.state.cpWidth;
            if (this.cards_system.length === 0) {
                clearInterval(this.clearInt);
                this.setState({ winner: "The Winner is...", cdWidth: 100, cpWidth: 100 });

                return;
            }
            this.cards_player.pop();
            this.cards_system.pop();
            this.setState({ playerCards: this.cards_player, systemCards: this.cards_system, cdWidth: this.wDc - 35, cpWidth: this.wPc - 35 });
        }, 100);
    }
    betHandler(e) {
        console.log("received in game " + e);
        this.betCard = e;
    }
    render() {
        return (
            <div
                style={{
                    width: `${this.width}`,
                    height: `${this.height}`,
                    backgroundImage: `url(${tableBg})`,
                    backgroundSize: "100% 100%",
                    backgroundRepeat: "no-repeat",
                    // border:"1px solid red"
                }}
            >
                <Stage width={this.width} height={this.height}>
                    {/* <Layer>
                        <Img image={dealer3D} x={window.innerHeight*0.5} y={0} ></Img>
                    </Layer> */}
                    <Layer>
                        <Group
                            width={this.width}
                            height={this.height}
                            ref={(node) => {
                                this.cardOne = node;
                            }}
                        >
                            <Img image={cardDisp} x={this.width - 100} y={0}></Img>
                            <Cards cards={this.state.systemCards} y={100}></Cards>
                            <Cards cards={this.state.playerCards} y={180}></Cards>
                            <Img ref={this.avtar_d_ref} image={dealerImg} x={0} y={100}></Img>
                            <Img ref={this.avtar_p_ref} image={userImg} x={0} y={180}></Img>
                            <Rect x={10} y={100} width={this.state.cdWidth} height={80} stroke="#00ff00"></Rect>
                            <Rect x={10} y={180} width={this.state.cpWidth} height={80} stroke="#00ff00"></Rect>
                            <Text x={this.state.cdWidth + 8} y={110} text="INSIDE" fontSize={16} rotation={90}></Text>
                            <Text x={this.state.cpWidth + 8} y={185} text="OUTSIDE" fontSize={16} rotation={90}></Text>
                            <Text x={100} y={280} text={this.state.winner} fontSize={16}></Text>
                        </Group>
                        <Group></Group>
                    </Layer>
                </Stage>
                <button
                    className="betButton"
                    onClick={(e) => {
                        e.preventDefault();
                        this.dealHandler(e);
                    }}
                >
                    Bet Ok
                </button>
                <BetPanel action={this.betHandler.bind(this)}></BetPanel>
            </div>
        );
    }
}
