import "./App.css";
import { BrowserRouter as Router, Route, Switch, Link, Redirect} from "react-router-dom";
import NavBar from "./comp/js/navBar";
import Home from "./comp/js/screens/home";
import Projects from "./comp/js/screens/projects";
import Contact from "./comp/js/screens/contact";
import About from "./comp/js/screens/about";

function App(props) {


    return (
        <div className="App">
            <NavBar props={props}></NavBar>
            
                <Switch>
                    <Route exact path="/portfolio">
                        <Redirect to="/home"></Redirect>
                    </Route>
                    <Route exact path="/home">
                        <Home></Home>
                    </Route>
                    <Route exact path="/about">
                        <About></About>
                    </Route>
                    <Route path="/projects">
                        <Projects></Projects>
                    </Route>
                    <Route exact path="/contact">
                        <Contact></Contact>
                    </Route>
                </Switch>
        </div>
    );
}

export default App;
