import { useState } from "react/cjs/react.development";
import "./index.css";
import Game from "./funCardGame/";

const FunCard = () => {
    const [show, setShow] = useState(false);
    const controller = () => {
        setShow(false);
    };
    return (
        <div className="in_out">
            {/* <h1>Welcome to Plinko</h1> */}
            {!show && (
                <div className="gameInfo">
                    <button
                        onClick={(e) => {
                            e.preventDefault();
                            setShow(true);
                        }}
                    >
                        Play Fun Card
                    </button>
                </div>
            )}
            {show && (
                <div className="game">
                    <Game width={800} height={400}></Game>
                    {/* show the game here */}
                </div>
            )}
        </div>
    );
};
export default FunCard;
