import { useState } from "react/cjs/react.development";
import Stage from "./plinkoGame/index";
import "./index.css";

const Plinko = () => {
    const [show, setShow] = useState(false);
    const controller=()=>{
        setShow(false)
    }
    return (
        <div className="plinko">
            {/* <h1>Welcome to Plinko</h1> */}
            {!show && (

            <div className="gameInfo">
                <button onClick={(e)=>{e.preventDefault();setShow(true)}}>Play Plinko</button>
            </div>
            )}
            {show && (
                <div className="game">
                    {/* <Stage width={800} height={600} controller={controller}></Stage> */}
                </div>
            )}
        </div>
    );
};
export default Plinko;
