import "../css/navBar.css";
import { useState } from "react";
import { useHistory} from "react-router";
import {FcHome} from "react-icons/fc";
const NavBar = (props) => {
    const [navBtn, setNavBtn] = useState(0);
    const histoty = useHistory();
    const navigationHandler = (path) => {
        console.log(path);
        console.log(props);
        switch (path) {
            case "home":
                histoty.push("/home");

                break;
            case "aboutMe":
                histoty.push("/about");
                break;
            case "projects":
                histoty.push("/projects");

                break;
            case "catchMe":
                histoty.push("/contact");

                break;
            default:
                histoty.push("/");

                break;
        }
    };
   
    return (
        <div className="NavBar">
            <h1>Navin Online</h1>
           <div className="NavBarElements">
           {/* <div className={navBtn === 0? "active" : "inActive"} onClick={(e)=>{navigationHandler("home");setNavBtn(0)}}>Home</div> */}
           <div onClick={(e)=>{navigationHandler("home");setNavBtn(0)}}><FcHome size={26} /></div>
           <div className={navBtn === 1? "active" : "inActive"}  onClick={(e)=>{navigationHandler("aboutMe");setNavBtn(1)}}>About Me</div>
           <div className={navBtn === 2? "active" : "inActive"} onClick={(e)=>{navigationHandler("projects");setNavBtn(2)}} >Projects</div>
           <div className={navBtn === 3? "active" : "inActive"} onClick={(e)=>{navigationHandler("catchMe");setNavBtn(3)}} >Catch Me</div>
           </div>           
        </div>
    );
};

export default NavBar;
