import Matter from "matter-js";

export default class Path {
    constructor(plinkos, br, target) {
        this.plinko = plinkos;
        this.barrierHeight = br;

        

        this.guides = [];

        switch (target) {
            case 0:
                this.r = [0, 1, 9, 14, 35, 27, 43, 53, 76, 89];
                this.ra = [-0.2, 0, 0.5, 0.5, 0.5, 0.9, 1.2, 1.2, 0.5, 0.5];
                this.l = [4, 5, 13, 20, 25, 35, 42, 44,54, 63, 64, 88];
                this.la = [-0.2, 0, 0.5, 0.7, 0.5, 0.9, 0.5,1.2, 1.4, 0.5, 0.8, 0];
                break;
            case 1:
                this.r = [0, 1, 9, 14, 35, 27, 44, 54, 77, 90];
                this.ra = [-0.2, 0, 0.5, 0.5, 0.5, 0.5, 0.5, 0.5, 0.5, 0.5];
                this.l = [4, 5, 13, 20, 26, 43, 64, 89];
                this.la = [-0.2, 0, 0.5, 0.7, 0.5, 0.5, 0.5, 0];
                break;
            case 2:
                this.r = [2, 4, 10, 15, 21, 28, 36, 55, 78, 91];
                this.ra = [0, 0, 0.2, 0.5, 0.45, 0.5, 0.6, 0.5, 0.5, 0.5];
                this.l = [1, 9, 20, 35, 54, 77, 90];
                this.la = [0, -0.3, 0, 0.5, 0.5, 0.5, 0];
                break;
            case 3:
                this.r = [2, 4, 10, 15, 21, 29, 37, 56, 67, 79, 92];
                this.ra = [0, 0, 0.2, 0.5, 0.3, 0, 0.75, 0.8, 0.5, 0.5, 0.5];
                this.l = [1, 9, 20, 28, 45, 46, 78, 91];
                this.la = [0, -0.25, 0, -0.3, 0.5, 0.6, 0.5, 0];
                break;
            case 4:
                this.r = [2, 4, 10, 15, 21, 29, 37, 56, 67, 78, 93];
                this.ra = [0, 0, 0.2, 0.5, 0.3, 0, 0.7, 0.8, 0.4, -0.5, 0];
                this.l = [1, 9, 20, 28, 45, 46, 66, 92];
                this.la = [0, -0.3, 0, -0.3, 0.5, 0.6, 0, 0];

                break;
            case 5:
                this.r = [0, 1, 14, 15, 29, 37, 46,55, 10, 68, 94];
                this.ra = [-0.2, 0, -0.2, 0.3, 0.25, 0.55, 0.5,-0.8, 0.2, 0, 0.2];
                this.l = [4, 9, 21, 22, 55, 36, 80, 81, 93, 45, 57,67];
                this.la = [-0.2, -0.3, -0.3, 0, -0.2, 0.5, -0.2, 0, 0, 0, 0,-0.2];
                break;
            case 6:
                this.r = [0, 1, 5,14, 15, 29,29, 37,37, 45, 46, 95, 10,10, 69];
                this.ra = [-0.35, 0,-0.5, -0.2, 0.2, 0.25,0.6, 0.55,1, -0.3, 0.25, 0.3, 0.2,1, -0.4];
                this.l = [4,4, 9, 21, 56, 68, 81, 82, 36,94];
                this.la = [-0.35,-0.9, -0.3, -0.2, -0.45, -0.5, -0.4, 0.3, 0.5,0];
                break;
            case 7:
                this.r = [0, 1, 10, 14, 15, 30, 48, 59, 83, 96];
                this.ra = [-0.2, 0, 0.2, -0.2, 0.3, 0.25, 0, -0.4, 0.4, 0.6];
                this.l = [4, 9, 21, 22, 37, 39, 47, 58, 71, 95];
                this.la = [-0.2, -0.5, -0.3, 0, -0.2, 0.3, -0.4, -0.5, 0, 0];
                break;
            case 8:
                this.r = [0, 1, 10, 14, 15, 30, 48, 59, 71, 97];
                this.ra = [-0.2, 0, 0.2, -0.2, 0.3, 0.25, 0, -0.4, -0.4, 0.3];
                this.l = [4, 9, 21, 22, 37, 39, 47, 58, 70, 84, 96];
                this.la = [-0.2, -0.5, -0.3, 0, -0.2, 0.3, -0.4, -0.6, -0.7, 0.3, 0];

                break;
            case 9:
                this.r = [0, 1, 10, 14, 15, 30, 48, 59, 71, 84, 98];
                this.ra = [-0.2, 0, 0.2, -0.2, 0.3, 0.25, 0, -0.4, -0.4, -0.4, 0];
                this.l = [4, 9, 21, 22, 37, 39, 47, 58, 70, 83, 97];
                this.la = [-0.2, -0.5, -0.3, 0, -0.2, 0.3, -0.4, -0.6, -0.7, -0.7, -0.5];

                break;
            case 10:
                this.r = [2, 4, 10, 14, 30, 60, 85, 99];
                this.ra = [0, 0, 0.2, -0.2, -0.5, -0.5, -0.5, 0];
                this.l = [1, 9, 21, 22, 29, 38, 39, 48, 59, 71, 84, 98];
                this.la = [0, -0.5, -0.3, 0, -0.5, -0.6, -0.3, -0.5, -0.5, -0.5, -0.5, -0.4];

                break;
            case 11:
                this.r = [2, 4, 10, 14, 30, 39, 59, 61, 86, 100];
                this.ra = [0, 0, 0.2, -0.2, -0.5, -0.5, -1, -0.5, -0.3, 0];
                this.l = [1, 9, 21, 22, 29, 38, 48, 60, 72, 85, 99];
                this.la = [0, -0.5, -0.3, 0, -0.5, -0.6, -1, -1.4, -0.5, -0.5, -0.5];
                break;
            case 12:
                this.r = [2, 4, 10, 49, 60, 62, 87, 101];
                this.ra = [0, 0, 0.2, -0.8, -0.95, -0.5, -0.5, 0];
                this.l = [1, 9, 15, 22, 30, 39, 61, 73, 86, 100];
                this.la = [0, -0.5, -0.5, -0.7, -0.5, -0.6, -1.45, -0.4, -0.4, -0.5];
                break;
            default:
                this.r = [0, 1, 14, 15, 29, 37, 45, 46, 95, 10, 69];
                this.ra = [-0.2, 0, -0.2, 0.2, 0.25, 0.55, -0.3, 0.5, 0.3, 0.2, -0.4];
                this.l = [4, 9, 21, 56, 68, 81, 82, 36];
                this.la = [-0.2, -0.5, -0.3, -0.2, -0.5, -0.4, 0.3, 0.5];
                break;
        }
    }
    createPath() {
        this.r.forEach((p, i) => {
            this.guides.push(this.createLineR(this.plinko[p].position.x, this.plinko[p].position.y, 1, this.barrierHeight, this.ra[i]));
        });
        this.l.forEach((p, i) => {
            // this.guides.push(this.createLineL(this.plinko[i].position.x,this.plinko[i].position.y,1,50,this.ra[i]));
            this.guides.push(this.createLineL(this.plinko[p].position.x, this.plinko[p].position.y, 1, this.barrierHeight, this.la[i]));
        });
        return this.guides;
    }

    createLineL(x, y, w, h, a) {
        return Matter.Bodies.rectangle(x - 3, y, w, h, {
            isStatic: true, //An immovable object
            label: "leftBar",
            angle: a,
            restitution: 0.7,
            render: {
                // visible: true,
                 visible: false,
                fillStyle: "#00ff00",
            },
        });
    }
    createLineR(x, y, w, h, a) {
        return Matter.Bodies.rectangle(x + 3, y, w, h, {
            isStatic: true, //An immovable object
            label: "rightBar",
            angle: a,
            restitution: 0.7,
            render: {
                // visible: true,
                visible: false,
                fillStyle: "#00ff00",
            },
        });
    }
}
